<template>
    <div class="wapper">

        <div class="operStage">
            <div class="table" v-if="show=='orderTable'">
                <el-table
                        :data="tableData"
                        border
                        tooltip-effect="dark"
                        style="width: 100%"
                        @select-all="handleSelectAll"
                        @select="handleCurrentChange">
                    <el-table-column
                            type="selection"
                            width="55">
                    </el-table-column>
                    <el-table-column
                            prop="id"
                            label="工单编号"
                            width="120"
                            show-overflow-tooltip>
                    </el-table-column>
                    <el-table-column
                            prop="title"
                            label="问题标题"
                            width="120"
                            show-overflow-tooltip>
                    </el-table-column>
                    <el-table-column
                            prop="issue"
                            label="问题内容"
                            width="120">
                    </el-table-column>
                    <el-table-column
                            prop="puddate"
                            label="提交时间"
                            show-overflow-tooltip>
                    </el-table-column>
                    <el-table-column
                            fixed="right"
                            label="操作"
                            width="100">
                        <template scope="scope">
                            <el-button @click="view(scope)" type="text" size="small">查看工单</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>

            <div class="order-display" v-if="show=='orderMessage'">
                <div>沟通记录</div>
                <div v-for="item in orderMessage">
                    {{item.message}}
                </div>
            </div>
        </div>
    </div>
</template>

<script type="text/babel">
    import util from '../util/util.js';
    export default{
        data(){
            return {
                tableData:[],
                selectRow:null,
                show:'orderTable',
                orderMessage:[{message:'问题一'},{message:'回答二'},{message:'问题二'},{message:'回答二'}]
            };
        },
        created(){
            var url = 'order/getOrders.do?type=pending';
            var attrOptions={
                tableData:'result'
            }
            util.get(url,this,attrOptions);
        },
        methods:{
            handleCurrentChange:util.handleCurrentChange,
            handleSelectAll:util.handleSelectAll,
            view(scope){
                /*var url = 'order/viewOrder.do?type='+scope.row.wc_sataus+"&orderid="+scope.row.id;
                var attrOptions={
                    orderMessage:'result'
                }
                util.get(url,this,attrOptions);*/
                this.show='orderMessage'
            }
        }
    }
</script>

<style scoped>
    .table{
        padding-top:15px;
    }
    .order-display{

    }
</style>


